.App {
  text-align: center;
}

.Container{
  width: 500px;
  height: 530px;
  border: 2px solid grey;
  border-radius: 15px;
}
.InputWindow{
  width: inherit;
  height: 50px;
  border-radius: 15px 15px 0px 0px;
  background-color: black;
  color: white;
  text-align: right;
  font-size: 2em;
}
.InputIsHere{
  padding-top:  5px;
  padding-right: 10px;
}
.Buttons{
  display:grid;
  grid-template-columns: 125px 125px 125px 125px;
}
.Opers{
  background-color: grey;
  width: 125px;
  height: 120px;
  border: 1px solid red;
  font-size: 3em;
}
.Equals{
  background-color: orange;
  border: 1px solid red;
  width: 125px;
  grid-column-start: 4;
  grid-column-end: 4;
  grid-row-start: 4;
  grid-row-end: 2;
  font-size: 3em;
}
.Numbers{
  width:125px;
  height: 120px;
  background-color: white;
  border: 1px solid red;
  font-size: 3em;
}
.Clear{
  background-color: red;
  color: white;
  font-size: 3em;
  border-radius: 0px 0px 15px 0px;
  border: 1px solid red;
}
#id1{
  border-radius: 0px 0px 0px 15px;
}
#id0{
  grid-column-start: 2;
}
